@import '../../globalVariable';

.accountInfo {
    background-color: #F1F1F1;
    height: 100vh;
    .block {
        background-color: #fff;
        margin-bottom: 12px;
        &.top {
            padding: 44px 143px 25px 144px;
        }
        .avator {
            width: 88px;
            height: 88px;
            img {
                width: 88px;
                height: 88px;
            }
        }
        .alias {
            margin-top: 12px;
            text-align: center;
            font-size: 19px;
            font-family: PingFangSC-Medium;
            line-height: 26px;
        }
        .line {
            height: 53px;
            margin-left: 25px;
            padding: 0 20px 0 5px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-bottom: 1px solid #EFF1F3;
            font-family: PingFangSC-Regular;
            .label {
                font-size: 17px;
            }
            .value {
                font-size: 16px;
                color: #7A8289;
            }
            .direct {
                height: 16px;
                width: 16px;
                background: url('../../assets/rightArrow.png');
                background-size: contain;
                background-repeat: no-repeat;
                @media screen and (-webkit-min-device-pixel-ratio: 2) {
                    background: url('../../assets/rightArrow@2x.png');
                    background-size: contain;
                    background-repeat: no-repeat;
        } 
            }
            
        }
    }
   
    .desc {
        font-size:20px;
        text-align: center;
    }

    .confirmBtn {
        margin-top: 40px;
    }

}

